import React from 'react';
import { Link } from 'react-router-dom';
import SocketService from '../services/SocketService';
import '../assets/css/Post/post.css'

import Like from '../cmps/Like'

export default class Post extends React.Component {
    state = {
        txt: "",
        comments: [],
        whoLiked: '',
        posts: [],
        users: []

    };
    componentDidMount() {
        this.setState({ posts: this.props.posts })
        this.setState({ users: this.props.users })

        SocketService.setup();
        SocketService.emit('post', this.props.post._id);
    }
    componentWillUnmount() {
        SocketService.terminate();
    }
    countComments = (post) => {
        let commentsCount = post.comments.length
        let result = ''
        if (commentsCount > 1) {
            result = 'show all ' + commentsCount + ' comments...';
        } else if (commentsCount === 0) {
            result = 'no comments'
        } else {
            result = 'show 1 comment...';
        }
        return result;
    }
    handleChange = (ev, postId) => {
        const { name, value } = ev.target;
        if (name === 'txt') {
            const addCommentBtn = document.querySelector(`.btn-${postId}`)
            if (value === '') {
                addCommentBtn.classList.remove('highligt');
            } else {
                addCommentBtn.classList.add('highligt');
            }
        }
        this.setState({ [name]: ev.target.value });
    };

    countLikes = (post) => {
        let likesCount = post.likedBy.length
        let result = ''
        if (likesCount > 1) {
            result = likesCount + ' likes';
        } else if (likesCount === 0) {
            result = ''
        } else {
            result = '1 like';
        }
        return result;
    }
    seeWhoLiked = (post, e) => {
        let elSpan = e.target
        let whoLiked = ''
        post.likedBy.forEach((user, i) => {
            if (i !== post.likedBy.length - 1 && i < 10) {
                whoLiked += user.nickName + ', \n'
            }
            else {
                whoLiked += user.nickName + ' '
                return;
            }
        });
        elSpan.lable = whoLiked;
        this.setState({ whoLiked: whoLiked })
    }
    toUserProfile = (user) => {
        this.props.history.push({
            pathname: `/user/${user._id}`,
            state: {
                selectedFriend: user,
                posts: this.state.posts,
                users: this.state.users,
            }
        });
    }
    toPost = (postId, post) => {
        this.props.history.push({
            pathname: `/post/${postId}/comments`,
            state: {
                post: post
            }
        });
    }

    sendComment = async (ev) => {
        ev.preventDefault();
        if (!this.state.txt) return

        let postId = this.props.post._id

        var date = new Date();
        var timestamp = date.getTime();
        var commentObj = {
            _id: '',
            createdAt: timestamp,
            txt: this.state.txt,
            owner: this.props.loggedInUser
        }
        const newComment = await this.props.addComment(commentObj, postId)
        SocketService.emit('post newComment', newComment);
        this.setState({ txt: '' })
    };


    render() {
        const { users, comments, post, loggedInUser, likePost, postOptionsClicked, onLikeClicked } = this.props;
        const { posts } = this.state

        return <article className="post-container">

            <div className="post-header">
                <div className="post-header-img-container flex row">
                    <div>
                        {/* <Link to={{ pathname: `/post/${post._id}/comments`, state: { post, loggedInUser } }}><img className="post-header-user-img" alt="to user page" src={post.owner.imgUrl}></img></Link> */}

                        <Link to={location => ({
                            ...location,
                            pathname: `/user/${post.owner._id}`,
                            state: {
                                posts: posts,
                                users: users
                            }
                        })}> <img className="post-header-user-img" alt="to user page" src={post.owner.imgUrl}></img>
                        </Link>




                        {/* <img className="post-header-user-img" alt="to user page" onClick={() => this.toUserProfile(post.owner)} src={post.owner.imgUrl}></img> */}
                        <span className="post-header-user-nickname">{post.owner.nickName}</span>
                    </div>
                    <img className="post-header-options-btn" onClick={() => postOptionsClicked(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1580064274/options_sulaqr.png" alt="options pic" title="options"></img>
                </div>
            </div>

            <div className="post-img-container">
                <img className="post-img" alt="pic url" src={post.imgUrl}></img>
            </div>

            <div className="post-body-container">
                <div className="post-buttons-container flex row">
                    {loggedInUser &&
                        <Like post={post} loggedInUser={loggedInUser} onLikeClicked={onLikeClicked} likePost={likePost} />}
                    {/* <Link to={`/post/${post._id}`}><img className="post-btn" alt="see post" title="see post" src={"https://res.cloudinary.com/ilya432/image/upload/v1579905174/comment_l3rb1o.png"}></img></Link> */}
                    <img className="post-btn" onClick={() => this.toPost(post._id, post)} alt="see post" title="see post" src={"https://res.cloudinary.com/ilya432/image/upload/v1579905174/comment_l3rb1o.png"}></img>
                    <img className="post-btn" alt="share" title="share" src={"https://res.cloudinary.com/ilya432/image/upload/v1579905163/share_owvhnz.png"}></img>
                </div>

                <div className="post-body-data-section flex row wrap">
                    {post.likedBy &&
                        <span className="post-show-likes" title={this.state.whoLiked} onMouseOver={(e) => this.seeWhoLiked(post, e)}>{this.countLikes(post)}</span>}
                </div>

                <div className="post-body-data flex col">

                    <div className="post-body-data-section flex row wrap">
                        <span className="post-body-user-nickname">{post.owner.nickName}</span>
                        <span className="post-body-user-txt">{post.txt}</span>
                        <span className="post-body-user-tags-span">{post.tags && <ul className="post-tags-list flex row wrap">
                            {post.tags.map((tag, i) => (
                                <li key={i}>
                                    <span className="post-body-user-tag">{'#' + tag}</span>
                                </li>
                            ))}
                        </ul>}</span>
                    </div>


                    <div className="post-body-data-section flex row">
                        {post.comments &&
                            // <Link to={`/post/${post._id}`}><span className="post-show-all-comments"> {this.countComments(post)}</span></Link>}
                            <span className="post-show-all-comments" onClick={() => this.toPost(post._id, post)}> {this.countComments(post)}</span>}
                    </div>

                    <div className="flex row">
                        {this.props.comments && <ul>
                            {comments.map(comment => (
                                <li key={comment._id}>
                                    <h3>{comment.txt}</h3>
                                    <p>
                                        <Link to={`user/${comment.aboutUser._id}`}> About {comment.aboutUser.username}</Link>
                                    </p>
                                    <p>
                                        <Link to={`user/${comment.byUser._id}`}> By {comment.byUser.username} </Link>
                                    </p>
                                </li>
                            ))}
                        </ul>}

                        {/* {users && loggedInUser &&
                            <select className="comment-select-user" onChange={this.handleChange} value={this.state.commentToEdit.aboutUserId} name="aboutUserId">
                                <option value="">Select User</option>
                                {users.map(user => (<option key={user._id} value={user._id}> {user.username} </option>
                                ))}
                            </select>
                        } */}







                        {loggedInUser && <form className="comment-container flex row" onSubmit={this.sendComment}>
                            <div className="add-comment-container">

                                <input className="add-comment-textarea" name="txt" type="text" placeholder="Add a comment..." autoComplete="off"
                                    onChange={(ev) => this.handleChange(ev, post._id)} value={this.state.txt}></input>


                                <div className="add-comment-container-absolute">
                                    <span className={`add-comment-button btn-${post._id}`} onClick={this.sendComment}>Post</span>
                                </div>
                            </div>
                        </form>}


                    </div>
                </div>
            </div>
        </article >
    }

}

