import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../assets/css/UserPage/userPage.css'
import { loadPosts } from '../actions/PostActions';
import { loadUsers } from '../actions/UserActions';
import UserService from '.././services/UserService'
// import { loadFriends, loadSelectedFriend } from '../actions/FriendActions.js';

class UserPage extends Component {

    state = {
        postsCount: 0,
        followersCount: 0,

        // posts: this.props.location.state.posts,
        // users: this.props.location.state.users,
        posts: [],
        users: [],
        user: {},
        // friends: []
    }


    componentDidMount = async () => {
        // const { posts, users } = this.props.location.state
        await this.props.loadPosts()
            .then((posts) => {
                this.setState({ posts })
                this.props.loadUsers()
                    .then((users) => {
                        this.setState({ users })
                        this.onLoadUser().then(() => {
                            this.countPosts(this.props.posts)
                            this.countFollowers(this.props.users, this.state.user)
                        })
                    })
            })
    }

    componentDidUpdate = () => {
        const newId = this.props.match.params.id
        if (this.state.user._id !== newId) {

            this.props.loadPosts()
                .then((posts) => {
                    this.setState({ posts })
                    this.props.loadUsers()
                        .then((users) => {
                            this.setState({ users })
                            this.onLoadUser().then(() => {
                                this.countPosts(this.props.posts)
                                this.countFollowers(this.props.users, this.state.user)
                            })
                        })
                })
        }
    }

    onLoadUser = async () => {
        const id = this.props.match.params.id
        await UserService.getById(id)
            .then((user) => {
                this.setState({ user: user })
            })
    }
    countPosts = (posts) => {
        // console.log('countPosts')
        let postsCount = 0
        posts.forEach((post) => {
            if (post.owner._id === this.state.user._id) {
                postsCount++
            }
        });
        let result = null
        if (postsCount > 1) {
            result = postsCount + ' posts';
        } else if (postsCount === 0) { result = 'no posts' }
        else { result = '1 post'; }
        this.setState(prevState => {
            return {
                ...prevState,
                postsCount: result
            };
        });
    }
    countFollowers = (users, selectedFriend) => {
        // console.log('countFollowers')
        let followersCount = 0
        users.forEach((user) => {
            if (user.following) {
                user.following.forEach((followedUser) => {
                    if (selectedFriend._id === followedUser._id) {
                        followersCount++
                    }
                })
            }
        });
        let result = ''
        if (followersCount > 1) {
            result = followersCount + ' followers';
        } else if (followersCount === 0) { result = 'no followers' }
        else { result = '1 follower'; }
        // this.setState({ followersCount: result })

        this.setState(prevState => {
            return {
                ...prevState,
                user: selectedFriend
            };
        });

        this.setState(prevState => {
            return {
                ...prevState,
                followersCount: result
            };
        });
    }

    render() {
        const { user, postsCount, followersCount } = this.state
        return (<div className="user-page-container">
            <div className="user-page-user-info">
                <div className="image-username-container flex row">
                    <img className="user-page-user-img" alt="user pic" src={user.imgUrl}></img>
                    <div className="user-social-info-container flex col">
                        <span className="username-txt">{user.username}</span>
                        <div className="social-info-section flex">
                            <span className="social-info-field postsCount">{postsCount}</span>
                            <span className="social-info-field followersCount">{followersCount}</span>
                            <span className="social-info-field folowing">folowing: </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        posts: state.post.posts,
        // friends: state.friend.friends,
        // selectedFriend: state.friend.selectedFriend,
        users: state.user.users,
        // user: state.user.user

    };
};
const mapDispatchToProps = {
    loadPosts,
    // loadFriends, loadSelectedFriend,
    loadUsers
};

export default connect(mapStateToProps, mapDispatchToProps)(UserPage);